//.osjs-gui-icon-view {}

.osjs-gui-icon-view-wrapper {
  flex-basis: 100%;
  flex-wrap: wrap;
  overflow: auto;
}

.osjs-gui-icon-view-entry {
  position: relative;
  display: inline-block;
  width: 6em;
  height: 7em;
  text-align: center;
  overflow: visible;
  z-index: 1;

  &.osjs__active {
    z-index: 2;

    .osjs__label {
      overflow: visible !important;
      height: auto !important;
    }
  }

  * {
    pointer-events: none;
  }

  & > .osjs__container {
    box-sizing: border-box;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    & > .osjs__image {
      padding: $base-margin / 2;

      & > .osjs-icon {
        width: 3em;
        height: 3em;
      }
    }

    & > .osjs__label {
      padding: $base-margin / 2;
      height: 2em;
      line-height: 1.2;
      overflow: hidden;
      word-break: break-all;

      & > span {
        display: block;
      }
    }
  }

  /*
  &:not(.osjs__active) .osjs__label {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  */
}
