.osjs-gui-expander {
  display: block;

  &-wrapper {
    display: flex;
    flex-direction: column;
  }

  &-header {
    flex: 1 1;
    display: flex;
    padding: $base-margin / 2;
    cursor: pointer;

    &-icon {
      flex: 0 0;
      min-width: 1em;
      margin-right: $base-margin / 2;

      &::after {
        content: '\25BC';
      }

      &[data-active="true"]::after {
        content: '\25B2';
      }
    }

    &-label {
      flex: 1 1;
    }
  }

  &-content {
    margin-top: $base-margin;
  }
}
