.osjs-gui-progressbar-wrapper {
  display: flex;
  z-index: 1;
  width: 100%;
  line-height: 2;
  padding: $base-margin / 2;
  text-align: center;
  min-height: $base-margin * 2;
  position: relative;
  box-sizing: border-box;
  flex-grow: 1;
}

.osjs-gui-progressbar-value {
  position: absolute;
  top: 0;
  left: 0;
  width: 0;
  height: 100%;
  z-index: 2;
}

.osjs-gui-progressbar-label {
  z-index: 3;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  & > span {
    display: inline-block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
