.osjs-gui-button {
  & > button {
    cursor: pointer;
    display: block;
    margin: 0;
    padding: $base-margin / 2;
    outline: 0 none;
    width: 100%;
    height: 100%;
    overflow: hidden;
    border: 0 none;
    background: transparent;
    box-sizing: border-box;
    font-family: inherit;
    font-size: inherit;
    white-space: nowrap;

    &::-moz-focus-inner {
      border: 0;
      padding: 0;
    }

    & > .osjs-icon + .osjs-label {
      margin-left: $base-margin / 2;
    }

    & > span {
      vertical-align: middle;
    }
  }
}
